import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { ConfirmDialogComponent } from '../confirm-dialog/confirm-dialog.component';
import { AirdropAddPopUpComponent } from '../airdrop-add-pop-up/airdrop-add-pop-up.component';
import { FirebaseDatabase } from '../firebase-database';


@Component({
  selector: 'app-publish-list-item',
  templateUrl: './publish-list-item.component.html',
  styleUrls: ['./publish-list-item.component.css']
})
export class PublishListItemComponent implements OnInit {

  puffOut = false;
  removed = false;
  registeredClicked = false;
  slidervalue = false;
  message;
  registered = false;

  constructor(public dialog: MatDialog) { }
  @Input() token: any;
  @Output() deleted = new EventEmitter();
  @Output() published = new EventEmitter();
  @Output() edited = new EventEmitter();
  @Output() referralAdded = new EventEmitter();

  ngOnInit() {
    if (this.token.published) {
      this.slidervalue = true;
    } else {
      this.slidervalue = false;
    }
  }

  remove() {
    this.makeConfirmationialog(`You really want to remove ${this.token.name} from list`, result => {
      if (result) {
        this.puffOut = true;
        setTimeout(() => {
          this.deleted.emit(this.token.id);
          this.removed = true;
        }, 1000);
      }
    });

  }
  editData() {
    const dialogRef = this.dialog.open(AirdropAddPopUpComponent , {
      width: '95%',
      maxHeight: '75%',
      height: '70%',
      maxWidth : '95%',
      data: this.token
    });

    dialogRef.afterClosed()
      .subscribe(airdropObject => {
        if (airdropObject != null) {
          this.edited.emit({object: airdropObject , id: this.token.id});
          this.show_message('Edited');
        }
      });
  }

  publish_toggle(event) {
    this.slidervalue = event.checked;
    const action = event.checked ? 'Publish' : 'Unpublish';
    this.makeConfirmationialog(`You really want to ${action} ${this.token.name}`, confirmation => {
      if (confirmation) {
        if (this.token.ref_url != null && this.token.ref_url !== '' ) {
          this.published.emit({status: event.checked, id: this.token.id });
        } else {
          this.show_message('Please add referal link before publishing');
          this.slidervalue = ! this.slidervalue;
        }

      } else {
        this.slidervalue = ! this.slidervalue;
      }
    });
  }

  makeConfirmationialog (message: string, callback: any ) {
    const dialogRef = this.dialog.open(ConfirmDialogComponent, {
      width: '300px',
      data: message
    });
    dialogRef.afterClosed().subscribe(callback);
  }
  show_message(message) {
      const that = this;
      this.message = message;
      setTimeout(() => {
        that.message = null;
      }, 3000);
  }
  addrefferal(value) {
    if (value) {
      this.token['ref_url'] = value;
      this.referralAdded.emit({ref_url: value, id: this.token.id});
      this.registered = true;
    }
  }
  getMinWidth() {
    if (window.innerWidth < 500) {
      return window.innerWidth * .9;
    }
    return 450;
  }

}



