import { Component, OnInit, Input } from '@angular/core';
import * as url from 'url';
import {Http} from '@angular/http';

@Component({
  selector: 'app-list-item',
  templateUrl: './list-item.component.html',
  styleUrls: ['./list-item.component.css']
})
export class ListItemComponent implements OnInit {

  @Input() token: any;
  imageUrl = 'https://steemit-production-imageproxy-upload.s3.amazonaws.com/DQmRuEJdjx6sQ2mEhCp5kXpe7xELfu3MHEmcgz8F5Z6dt7c';

  constructor(public http: Http) {
  }
  ngOnInit() {
    this.getImage();
  }

  getImage() {
    if (this.token['icon_url']) {
      const imageFormats = ['.jpeg', '.jpg', '.png', '.svg', '.ico', '.bmp', '.gif'];
      const airdrop_url: string = this.token['icon_url'];
      let isImageUrl = false;
      for (let index = 0; index < imageFormats.length; index++) {
        const element = imageFormats[index];

        if (airdrop_url.indexOf(element) !== -1) {
          isImageUrl = true;
          break;
        }
      }

      if (isImageUrl) {
        this.imageUrl = airdrop_url;
      } else {
          this.getFaviconUrl(airdrop_url);
      }

    }
   }

   getFaviconUrl(uri: string) {
    const url_object = url.parse(uri);
    const fav_url = 'http://' + url_object.host + '/favicon.ico';
    this.http.get(fav_url)
      .subscribe((response) => {
        if (!(response.status > 400)) {
          this.imageUrl = fav_url;
        }
      } );
   }

   getTime() {

    if (! this.token.endDate) {
      return null;
    }

      const now: any = new Date();
      const then: any = new Date(this.token.endDate);
      let outString = '';
      let diff: any = (then - now) / 1000;

      if (diff < 1) { return 'Event Over'; }

      const days_remaining = Math.ceil(diff / 60 / 60 / 24);
      diff = -diff + (days_remaining * 60 * 60 * 24);
      const hrs_remaining = Math.ceil(diff / 60 / 60);
      diff = -diff + (hrs_remaining * 60 * 60);
      const mins_remaining = Math.ceil(diff / 60);

      outString = `${days_remaining} Days : ${hrs_remaining} Hrs : ${mins_remaining} mins Remining`;
      return outString;
  }


}




