import { Component, OnInit } from '@angular/core';
import { FirebaseDatabase } from '../firebase-database';

@Component({
  selector: 'app-publish',
  templateUrl: './publish.component.html',
  styleUrls: ['./publish.component.css']
})
export class PublishComponent implements OnInit {

  database;
  airdrops_to_publish = [];
  airdrop_objects = {};
  cols = 1;

  constructor() {
    this.database = new FirebaseDatabase().getInstance();
    this.getCols();
   }

  ngOnInit() {
    this.update_publised_list();
  }
  getCols() {
    const that = this;
    setInterval(() => {
      if (that.airdrops_to_publish.length > 1) {
        that.cols =  Math.ceil((window.innerWidth - 100) / 490);
      }
    }, 1000);
  }

  removeFromFirebase(id) {
    const object = {};
    object[id] = null;
    this.database.ref('toPublish/').update(object);
  }

  changePublish(object) {
    const update = {published : object.status};
    this.database.ref('toPublish/' + object.id + '/' ).update(update);
  }

  update_edited (object) {
    const id = object.id;
    const update = object.object;
    update[id] = id;
    this.database.ref('toPublish/' + id + '/').update(update);
  }

  add_referral (object) {
      const refferal = object.ref_url;
      const update = {ref_url: refferal, ref_addedon: new Date()} ;
      this.database.ref('toPublish/' + object.id + '/').update(update);
  }

  update_publised_list() {
    const publishRef = this.database.ref('toPublish/');
    publishRef.on('value', result => {
      this.airdrops_to_publish = [];
        this.airdrop_objects = result.val();
        for (const key in this.airdrop_objects) {
          if (this.airdrop_objects.hasOwnProperty(key)) {
            const element = this.airdrop_objects[key];
            this.airdrops_to_publish.push(element);
          }
        }
        console.log(this.airdrops_to_publish);
    });
  }

  getHeight() {
    if (window.innerWidth <= 450) {
      return 200;
    } else {
      return 300;
    }
  }

}



