import { Component, OnInit } from '@angular/core';
import { AirdropAddPopUpComponent } from '../airdrop-add-pop-up/airdrop-add-pop-up.component';
import { MatDialog } from '@angular/material';
import { FirebaseDatabase } from '../firebase-database';


@Component({
  selector: 'app-home-component',
  templateUrl: './home-component.component.html',
  styleUrls: ['./home-component.component.css']
})
export class HomeComponent  {


  success: String;
  tokens = null;
  airdrops = {};
  error;
  database;

  constructor( public dialog: MatDialog) {
    this.database = new FirebaseDatabase().getInstance();
    const airdropsRef = this.database.ref('toPublish/');
    airdropsRef.on('value', result => {
      this.airdrops = result.val();
      this.tokens = [];
      for (const key in this.airdrops) {
        if (this.airdrops.hasOwnProperty(key)) {
          const element = this.airdrops[key];
          if (element.published) {
              console.log(element);
              this.tokens.push(element);
              this.tokens.reverse();
          }
        }
      }
    });
  }

  addAirdrop( ) {
    const dialogRef = this.dialog.open(AirdropAddPopUpComponent, {
      width: '50%',
      maxWidth: '99vw',
      height: '80%'
    });

    dialogRef.afterClosed().subscribe(result => {
      const update = {};
      const newPostkey = this.randomString(5, '#aA');
      update[newPostkey] = JSON.parse(JSON.stringify(result));
      update[newPostkey]['id'] = newPostkey;
      update[newPostkey]['addedOn'] = new Date().toDateString();
      this.database.ref('toPublish/').update(update);
      this.displaySuccess('Successfully Added');
    });
  }
  randomString(length, chars) {
    let mask = '';
    if (chars.indexOf('a') > -1) { mask += 'abcdefghijklmnopqrstuvwxyz'; }
    if (chars.indexOf('A') > -1) { mask += 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'; }
    if (chars.indexOf('#') > -1) { mask += '0123456789'; }
    if (chars.indexOf('!') > -1) { mask += '~`!@#$%^&*()_+-={}[]:";\'<>?,./|\\'; }
    let result = '';
    for (let i = length; i > 0; --i) { result += mask[Math.floor(Math.random() * mask.length)]; }
    return result;
}

displaySuccess(message: String ) {
  const that = this;
  this.success = message;
  setTimeout(() => {
    that.success = null;
  }, 5000);
}

  displayError(message: String ) {
    const that = this;
    this.error = message;
    setTimeout(() => {
      that.error = null;
    }, 5000);
  }

}



