import { BrowserModule } from '@angular/platform-browser';
import { NgModule, Component } from '@angular/core';
import {HttpModule} from '@angular/http';
import { ReactiveFormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';
import { ConfirmDialogComponent } from './confirm-dialog/confirm-dialog.component';

import { AppComponent } from './app.component';
import { ListItemComponent } from './list-item/list-item.component';
import { AirdropAddPopUpComponent } from './airdrop-add-pop-up/airdrop-add-pop-up.component';
import { PublishComponent } from './publish/publish.component';
import { PublishListItemComponent } from './publish-list-item/publish-list-item.component';
import { HomeComponent } from './home-component/home-component.component';

import {MaterialImportModule} from './material-import/material-import.module';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';

import { OwlDateTimeModule, OwlNativeDateTimeModule } from 'ng-pick-datetime';
import {TimeAgoPipe} from 'time-ago-pipe';


@NgModule({
  declarations: [
    AppComponent,
    ListItemComponent,
    AirdropAddPopUpComponent,
    TimeAgoPipe,
    PublishComponent,
    PublishListItemComponent,
    HomeComponent,
    ConfirmDialogComponent
  ],
  imports: [
    BrowserModule,
    MaterialImportModule,
    HttpModule,
    BrowserAnimationsModule,
    OwlDateTimeModule,
    OwlNativeDateTimeModule,
    ReactiveFormsModule,
    RouterModule.forRoot([
      {path: '', component: HomeComponent},
      {path: 'publish', component: PublishComponent}
    ])
  ],
  entryComponents: [AirdropAddPopUpComponent, ConfirmDialogComponent],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



